'use client';

import React, { useEffect } from 'react';
import { AppLayout } from '@/components/layout/AppLayout';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Separator } from '@/components/ui/separator';
import { FloorEditorCanvas } from '@/components/table-management/FloorEditorCanvas';
import { TablePropertiesPanel } from '@/components/table-management/TablePropertiesPanel';
import { SaveDraftButton } from '@/components/table-management/SaveDraftButton';
import { ActivateLayoutButton } from '@/components/table-management/ActivateLayoutButton';
import { useTableStore } from '@/lib/stores/tableStore';
import { Plus, Square, Circle, RectangleHorizontal, Sparkles } from 'lucide-react';

export default function TableManagementPage() {
  const { 
    addTable, 
    selectedTable, 
    hasOverlap, 
    hasDraft,
    canvasSize 
  } = useTableStore();

  // Add table handlers
  const addRoundTable = () => {
    addTable({
      x: 100,
      y: 100,
      w: 60,
      h: 60,
      shape: 'round',
      capacity: 4,
    });
  };

  const addSquareTable = () => {
    addTable({
      x: 200,
      y: 100,
      w: 60,
      h: 60,
      shape: 'square',
      capacity: 4,
    });
  };

  const addRectTable = () => {
    addTable({
      x: 300,
      y: 100,
      w: 100,
      h: 60,
      shape: 'rect',
      capacity: 6,
    });
  };

  const isOverlapping = hasOverlap();

  return (
    <AppLayout pageTitle="Table Management">
      <div className="container mx-auto p-6">
        {/* Header */}
        <div className="mb-6">
          <div className="flex items-center justify-between mb-4">
            <div>
              <h1 className="text-3xl font-bold text-gray-900">Table Management</h1>
              <p className="text-muted-foreground mt-1">
                Design your restaurant floor layout with AI-powered Agent assistance
              </p>
            </div>
            
            <div className="flex items-center gap-2">
              <SaveDraftButton />
              <ActivateLayoutButton disabled={isOverlapping} />
            </div>
          </div>

          {/* Status indicators */}
          <div className="flex items-center gap-4 mb-4">
            <div className="flex items-center gap-2">
              <Badge variant="outline" className="bg-green-50 text-green-700 border-green-200">
                <Circle className="w-3 h-3 mr-1 fill-current" />
                Free
              </Badge>
              <Badge variant="outline" className="bg-blue-50 text-blue-700 border-blue-200">
                <Circle className="w-3 h-3 mr-1 fill-current" />
                Seated
              </Badge>
              <Badge variant="outline" className="bg-gray-50 text-gray-700 border-gray-200">
                <Circle className="w-3 h-3 mr-1 fill-current" />
                Dirty
              </Badge>
              <Badge variant="outline" className="bg-yellow-50 text-yellow-700 border-yellow-200">
                <Circle className="w-3 h-3 mr-1 fill-current" />
                Reserved
              </Badge>
            </div>
            
            {isOverlapping && (
              <Badge variant="destructive" className="animate-pulse">
                ⚠️ Overlapping tables detected
              </Badge>
            )}
            
            {hasDraft && (
              <Badge variant="secondary">
                📝 Unsaved draft
              </Badge>
            )}
          </div>

          {/* Add table controls */}
          <Card className="mb-6">
            <CardHeader>
              <CardTitle className="text-lg flex items-center gap-2">
                <Plus className="w-5 h-5" />
                Add Tables
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="flex gap-3">
                <Button 
                  variant="outline" 
                  onClick={addRoundTable}
                  className="flex items-center gap-2"
                >
                  <Circle className="w-4 h-4" />
                  Round (60×60)
                </Button>
                <Button 
                  variant="outline" 
                  onClick={addSquareTable}
                  className="flex items-center gap-2"
                >
                  <Square className="w-4 h-4" />
                  Square (60×60)
                </Button>
                <Button 
                  variant="outline" 
                  onClick={addRectTable}
                  className="flex items-center gap-2"
                >
                  <RectangleHorizontal className="w-4 h-4" />
                  Rectangle (100×60)
                </Button>
              </div>
              <p className="text-sm text-muted-foreground mt-2">
                💡 <strong>Smart Agent tip:</strong> Use arrow keys to nudge selected tables. Tables snap to 8px grid automatically.
              </p>
            </CardContent>
          </Card>
        </div>

        {/* Main layout */}
        <div className="grid grid-cols-1 lg:grid-cols-4 gap-6">
          {/* Canvas */}
          <div className="lg:col-span-3">
            <Card className="h-full">
              <CardHeader>
                <CardTitle className="flex items-center justify-between">
                  <span>Floor Layout Canvas</span>
                  <div className="text-sm text-muted-foreground">
                    {canvasSize.width} × {canvasSize.height}px
                  </div>
                </CardTitle>
              </CardHeader>
              <CardContent className="p-0">
                <FloorEditorCanvas />
              </CardContent>
            </Card>
          </div>

          {/* Properties Panel */}
          <div className="lg:col-span-1">
            <Card className="sticky top-6">
              <CardHeader>
                <CardTitle className="flex items-center gap-2">
                  <Sparkles className="w-5 h-5" />
                  Table Properties
                </CardTitle>
              </CardHeader>
              <CardContent>
                {selectedTable ? (
                  <TablePropertiesPanel />
                ) : (
                  <div className="text-center py-8 text-muted-foreground">
                    <div className="text-4xl mb-3">🍽️</div>
                    <p className="text-sm">
                      Select a table to edit its properties
                    </p>
                    <p className="text-xs mt-2">
                      Click any table on the canvas to get started
                    </p>
                  </div>
                )}
              </CardContent>
            </Card>
          </div>
        </div>

        {/* Success message after activation */}
        <div className="mt-6">
          <Card className="bg-gradient-to-r from-green-50 to-blue-50 border-green-200">
            <CardContent className="pt-6">
              <div className="flex items-center gap-3">
                <div className="text-2xl">🎉</div>
                <div>
                  <h3 className="font-semibold text-green-800">What you just unlocked</h3>
                  <p className="text-sm text-green-700">
                    Seating errors ↓ 40% • Save 4–6 hours/month • Real-time status tracking • QR code integration ready
                  </p>
                </div>
              </div>
            </CardContent>
          </Card>
        </div>
      </div>
    </AppLayout>
  );
}
