'use client';

import React, { useState } from 'react';
import { Button } from '@/components/ui/button';
import { 
  Dialog, 
  DialogContent, 
  DialogDescription, 
  DialogHeader, 
  DialogTitle, 
  DialogTrigger,
  DialogFooter,
  DialogClose
} from '@/components/ui/dialog';
import { Badge } from '@/components/ui/badge';
import { Play, CheckCircle, AlertCircle, Zap } from 'lucide-react';
import { useTableStore } from '@/lib/stores/tableStore';
import { cn } from '@/lib/utils';

interface ActivateLayoutButtonProps {
  className?: string;
}

export function ActivateLayoutButton({ className }: ActivateLayoutButtonProps) {
  const { tables, zones, hasOverlaps } = useTableStore();
  const [isActivating, setIsActivating] = useState(false);
  const [activateStatus, setActivateStatus] = useState<'idle' | 'success' | 'error'>('idle');
  const [isDialogOpen, setIsDialogOpen] = useState(false);

  const handleActivateLayout = async () => {
    if (isActivating) return;
    
    setIsActivating(true);
    setActivateStatus('idle');

    try {
      // Validate layout before activation
      if (tables.length === 0) {
        throw new Error('Layout must contain at least one table');
      }

      if (hasOverlaps()) {
        throw new Error('Cannot activate layout with overlapping tables');
      }

      // Prepare activation data
      const activationData = {
        tables,
        zones,
        metadata: {
          version: '1.0',
          activatedAt: new Date().toISOString(),
          activatedBy: 'current-user', // TODO: Get from auth context
          tableCount: tables.length,
          zoneCount: zones.length,
        }
      };

      // Activate via API
      const response = await fetch('/api/pos/floor/layout/activate', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'x-tenant-id': 'default', // TODO: Get from auth context
        },
        body: JSON.stringify(activationData),
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.message || 'Failed to activate layout');
      }

      setActivateStatus('success');
      setIsDialogOpen(false);
      
      // Auto-clear success status after 5 seconds
      setTimeout(() => {
        setActivateStatus('idle');
      }, 5000);

    } catch (error) {
      console.error('Error activating layout:', error);
      setActivateStatus('error');
      
      // Auto-clear error status after 5 seconds
      setTimeout(() => {
        setActivateStatus('idle');
      }, 5000);
    } finally {
      setIsActivating(false);
    }
  };

  const getButtonContent = () => {
    switch (activateStatus) {
      case 'success':
        return (
          <>
            <CheckCircle className="w-4 h-4 mr-2" />
            Layout Activated
          </>
        );
      case 'error':
        return (
          <>
            <AlertCircle className="w-4 h-4 mr-2" />
            Activation Failed
          </>
        );
      default:
        return (
          <>
            <Play className="w-4 h-4 mr-2" />
            {isActivating ? 'Activating...' : 'Activate Layout'}
          </>
        );
    }
  };

  const getButtonVariant = () => {
    switch (activateStatus) {
      case 'success':
        return 'default';
      case 'error':
        return 'destructive';
      default:
        return 'default';
    }
  };

  const isDisabled = isActivating || tables.length === 0 || hasOverlaps();

  const validationIssues = [];
  if (tables.length === 0) validationIssues.push('No tables in layout');
  if (hasOverlaps()) validationIssues.push('Tables overlap');

  return (
    <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
      <DialogTrigger asChild>
        <Button
          variant={getButtonVariant()}
          disabled={isDisabled}
          className={cn(
            'h-9 px-4 text-sm font-medium transition-all',
            activateStatus === 'success' && 'bg-green-100 text-green-800 border-green-300 hover:bg-green-200',
            activateStatus === 'error' && 'bg-red-100 text-red-800 border-red-300 hover:bg-red-200',
            className
          )}
        >
          {getButtonContent()}
        </Button>
      </DialogTrigger>
      
      <DialogContent className="sm:max-w-md">
        <DialogHeader>
          <DialogTitle className="flex items-center gap-2">
            <Zap className="w-5 h-5 text-orange-500" />
            Activate Floor Layout
          </DialogTitle>
          <DialogDescription>
            This will make the current layout active for POS operations. 
            The previous active layout will be archived.
          </DialogDescription>
        </DialogHeader>
        
        <div className="space-y-4">
          {/* Layout Summary */}
          <div className="bg-gray-50 rounded-lg p-3 space-y-2">
            <h4 className="text-sm font-medium">Layout Summary</h4>
            <div className="flex flex-wrap gap-2 text-xs">
              <Badge variant="outline">
                {tables.length} {tables.length === 1 ? 'Table' : 'Tables'}
              </Badge>
              <Badge variant="outline">
                {zones.length} {zones.length === 1 ? 'Zone' : 'Zones'}
              </Badge>
              <Badge variant="outline">
                Total Capacity: {tables.reduce((sum: number, table: any) => sum + (table.capacity || table.seats || 4), 0)} seats
              </Badge>
            </div>
          </div>

          {/* Validation Status */}
          <div className="space-y-2">
            <h4 className="text-sm font-medium">Validation</h4>
            {validationIssues.length === 0 ? (
              <div className="flex items-center gap-2 text-green-600 text-sm">
                <CheckCircle className="w-4 h-4" />
                Layout is ready for activation
              </div>
            ) : (
              <div className="space-y-1">
                {validationIssues.map((issue, index) => (
                  <div key={index} className="flex items-center gap-2 text-red-600 text-sm">
                    <AlertCircle className="w-4 h-4" />
                    {issue}
                  </div>
                ))}
              </div>
            )}
          </div>

          {/* Warning */}
          <div className="bg-orange-50 border border-orange-200 rounded-lg p-3">
            <div className="flex items-start gap-2">
              <AlertCircle className="w-4 h-4 text-orange-500 mt-0.5" />
              <div className="text-sm text-orange-700">
                <p className="font-medium">Important Notice</p>
                <p>Activating this layout will immediately affect POS operations. 
                Make sure the layout is tested and ready for use.</p>
              </div>
            </div>
          </div>
        </div>

        <DialogFooter className="gap-2">
          <DialogClose asChild>
            <Button variant="outline" size="sm">
              Cancel
            </Button>
          </DialogClose>
          <Button 
            onClick={handleActivateLayout}
            disabled={isActivating || validationIssues.length > 0}
            size="sm"
            className="bg-orange-500 hover:bg-orange-600 text-white"
          >
            <Zap className="w-4 h-4 mr-2" />
            {isActivating ? 'Activating...' : 'Activate Layout'}
          </Button>
        </DialogFooter>
      </DialogContent>
    </Dialog>
  );
}
