'use client';

import React from 'react';
import { useTableStore, type Table as TableNodeType } from '@/contexts/tableStore';
import { ResizeHandle } from './ResizeHandle';
import { TableLabel } from './TableLabel';
import { TableStatusBadge } from './TableStatusBadge';
import { cn } from '@/lib/utils';

interface TableNodeProps {
  table: TableNodeType;
  isSelected: boolean;
  isDragging: boolean;
  onMouseDown: (e: React.MouseEvent) => void;
  onClick: () => void;
}

export function TableNode({ 
  table, 
  isSelected, 
  isDragging, 
  onMouseDown, 
  onClick 
}: TableNodeProps) {
  const { hasOverlap, tableStates } = useTableStore();
  
  const tableStatus = tableStates[table.id]?.status || 'FREE';
  const isOverlapping = hasOverlap(table.id);

  // Get shape-specific styles
  const getShapeStyles = () => {
    const baseStyles = {
      width: table.w,
      height: table.h,
      left: table.x,
      top: table.y,
    };

    switch (table.shape) {
      case 'round':
        return {
          ...baseStyles,
          borderRadius: '50%',
        };
      case 'square':
        return {
          ...baseStyles,
          borderRadius: '8px',
        };
      case 'rect':
        return {
          ...baseStyles,
          borderRadius: '12px',
        };
      default:
        return {
          ...baseStyles,
          borderRadius: '8px',
        };
    }
  };

  // Get status color
  const getStatusColor = () => {
    switch (tableStatus) {
      case 'FREE':
        return 'bg-green-100 border-green-300 hover:bg-green-200';
      case 'SEATED':
        return 'bg-blue-100 border-blue-300 hover:bg-blue-200';
      case 'DIRTY':
        return 'bg-gray-100 border-gray-300 hover:bg-gray-200';
      case 'RESERVED':
        return 'bg-yellow-100 border-yellow-300 hover:bg-yellow-200';
      default:
        return 'bg-white border-gray-300 hover:bg-gray-50';
    }
  };

  const shapeStyles = getShapeStyles();

  return (
    <div
      className={cn(
        'absolute cursor-pointer border-2 transition-all duration-150 flex items-center justify-center text-sm font-semibold',
        getStatusColor(),
        isSelected && 'ring-2 ring-blue-500 ring-offset-2 z-10',
        isDragging && 'opacity-75 z-20',
        isOverlapping && 'border-red-500 bg-red-100 animate-pulse'
      )}
      style={shapeStyles}
      onMouseDown={onMouseDown}
      onClick={(e) => {
        e.stopPropagation();
        onClick();
      }}
    >
      {/* Table Label */}
      <TableLabel tableId={table.id} />
      
      {/* Status Badge */}
      <TableStatusBadge 
        status={tableStatus} 
        className="absolute -top-2 -right-2"
      />
      
      {/* Resize Handles - only show when selected */}
      {isSelected && (
        <>
          <ResizeHandle 
            tableId={table.id}
            position="bottom-right"
            className="absolute -bottom-1 -right-1"
          />
          <ResizeHandle 
            tableId={table.id}
            position="bottom"
            className="absolute -bottom-1 left-1/2 transform -translate-x-1/2"
          />
          <ResizeHandle 
            tableId={table.id}
            position="right"
            className="absolute -right-1 top-1/2 transform -translate-y-1/2"
          />
        </>
      )}

      {/* Zone indicator */}
      {table.zoneId && (
        <div className="absolute -top-1 -left-1 w-3 h-3 rounded-full border-2 border-white"
             style={{ backgroundColor: 'var(--zone-color, #3B82F6)' }} />
      )}
    </div>
  );
}
