'use client';

import React, { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Save, CheckCircle, AlertCircle } from 'lucide-react';
import { useTableStore } from '@/lib/stores/tableStore';
import { cn } from '@/lib/utils';

interface SaveDraftButtonProps {
  className?: string;
}

export function SaveDraftButton({ className }: SaveDraftButtonProps) {
  const { tables, zones, hasOverlaps } = useTableStore();
  const [isSaving, setIsSaving] = useState(false);
  const [saveStatus, setSaveStatus] = useState<'idle' | 'success' | 'error'>('idle');

  const handleSaveDraft = async () => {
    if (isSaving) return;
    
    setIsSaving(true);
    setSaveStatus('idle');

    try {
      // Validate layout before saving
      if (tables.length === 0) {
        throw new Error('Layout must contain at least one table');
      }

      if (hasOverlaps()) {
        throw new Error('Cannot save layout with overlapping tables');
      }

      // Prepare layout data
      const layoutData = {
        tables,
        zones,
        metadata: {
          version: '1.0',
          lastModified: new Date().toISOString(),
          tableCount: tables.length,
          zoneCount: zones.length,
        }
      };

      // Save to API
      const response = await fetch('/api/pos/floor/layout/draft', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'x-tenant-id': 'default', // TODO: Get from auth context
        },
        body: JSON.stringify(layoutData),
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.message || 'Failed to save draft');
      }

      setSaveStatus('success');
      
      // Auto-clear success status after 3 seconds
      setTimeout(() => {
        setSaveStatus('idle');
      }, 3000);

    } catch (error) {
      console.error('Error saving draft:', error);
      setSaveStatus('error');
      
      // Auto-clear error status after 5 seconds
      setTimeout(() => {
        setSaveStatus('idle');
      }, 5000);
    } finally {
      setIsSaving(false);
    }
  };

  const getButtonContent = () => {
    switch (saveStatus) {
      case 'success':
        return (
          <>
            <CheckCircle className="w-4 h-4 mr-2" />
            Draft Saved
          </>
        );
      case 'error':
        return (
          <>
            <AlertCircle className="w-4 h-4 mr-2" />
            Save Failed
          </>
        );
      default:
        return (
          <>
            <Save className="w-4 h-4 mr-2" />
            {isSaving ? 'Saving...' : 'Save Draft'}
          </>
        );
    }
  };

  const getButtonVariant = () => {
    switch (saveStatus) {
      case 'success':
        return 'default';
      case 'error':
        return 'destructive';
      default:
        return 'outline';
    }
  };

  const isDisabled = isSaving || tables.length === 0 || hasOverlaps();

  return (
    <Button
      variant={getButtonVariant()}
      onClick={handleSaveDraft}
      disabled={isDisabled}
      className={cn(
        'h-9 px-4 text-sm font-medium transition-all',
        saveStatus === 'success' && 'bg-green-100 text-green-800 border-green-300 hover:bg-green-200',
        saveStatus === 'error' && 'bg-red-100 text-red-800 border-red-300 hover:bg-red-200',
        className
      )}
    >
      {getButtonContent()}
    </Button>
  );
}
