'use client';

import React from 'react';
import { useTableStore } from '@/contexts/tableStore';

export function GridLayer() {
  const { canvasSize, gridSize, snapToGrid } = useTableStore();

  if (!snapToGrid) return null;

  const horizontalLines = Math.floor(canvasSize.height / gridSize);
  const verticalLines = Math.floor(canvasSize.width / gridSize);

  return (
    <div className="absolute inset-0 pointer-events-none opacity-20">
      {/* Horizontal lines */}
      {Array.from({ length: horizontalLines + 1 }, (_, i) => (
        <div
          key={`h-${i}`}
          className="absolute w-full border-t border-gray-300"
          style={{ top: i * gridSize }}
        />
      ))}
      
      {/* Vertical lines */}
      {Array.from({ length: verticalLines + 1 }, (_, i) => (
        <div
          key={`v-${i}`}
          className="absolute h-full border-l border-gray-300"
          style={{ left: i * gridSize }}
        />
      ))}
      
      {/* Grid dots at intersections */}
      {Array.from({ length: horizontalLines + 1 }, (_, i) =>
        Array.from({ length: verticalLines + 1 }, (_, j) => (
          <div
            key={`dot-${i}-${j}`}
            className="absolute w-1 h-1 bg-gray-400 rounded-full transform -translate-x-0.5 -translate-y-0.5"
            style={{
              top: i * gridSize,
              left: j * gridSize,
            }}
          />
        ))
      )}
    </div>
  );
}
