'use client';

import React, { useRef, useEffect, useState, useCallback } from 'react';
import { useTableStore } from '@/contexts/tableStore';
import { TableNode } from '@/components/table-management/TableNode';
import { GridLayer } from '@/components/table-management/GridLayer';
import { DragController } from '@/components/table-management/DragController';

export function FloorEditorCanvas() {
  const canvasRef = useRef<HTMLDivElement>(null);
  const {
    tables,
    zones,
    selectedTable,
    selectTable,
    moveTable,
    canvasSize,
    gridSize,
    snapToGrid,
  } = useTableStore();

  const [dragState, setDragState] = useState<{
    isDragging: boolean;
    tableId: string | null;
    startPos: { x: number; y: number };
    offset: { x: number; y: number };
  }>({
    isDragging: false,
    tableId: null,
    startPos: { x: 0, y: 0 },
    offset: { x: 0, y: 0 },
  });

  // Handle keyboard navigation
  useEffect(() => {
    const handleKeyDown = (e: KeyboardEvent) => {
      if (!selectedTable) return;

      const step = snapToGrid ? gridSize : 1;
      let newX = selectedTable.x;
      let newY = selectedTable.y;

      switch (e.key) {
        case 'ArrowLeft':
          e.preventDefault();
          newX = Math.max(0, selectedTable.x - step);
          break;
        case 'ArrowRight':
          e.preventDefault();
          newX = Math.min(canvasSize.width - selectedTable.w, selectedTable.x + step);
          break;
        case 'ArrowUp':
          e.preventDefault();
          newY = Math.max(0, selectedTable.y - step);
          break;
        case 'ArrowDown':
          e.preventDefault();
          newY = Math.min(canvasSize.height - selectedTable.h, selectedTable.y + step);
          break;
        case 'Escape':
          e.preventDefault();
          selectTable(null);
          break;
        default:
          return;
      }

      if (newX !== selectedTable.x || newY !== selectedTable.y) {
        moveTable(selectedTable.id, newX, newY);
      }
    };

    window.addEventListener('keydown', handleKeyDown);
    return () => window.removeEventListener('keydown', handleKeyDown);
  }, [selectedTable, moveTable, selectTable, snapToGrid, gridSize, canvasSize]);

  // Handle mouse events for dragging
  const handleMouseDown = useCallback((e: React.MouseEvent, tableId: string) => {
    e.preventDefault();
    e.stopPropagation();

    const table = tables.find(t => t.id === tableId);
    if (!table) return;

    const rect = canvasRef.current?.getBoundingClientRect();
    if (!rect) return;

    const mouseX = e.clientX - rect.left;
    const mouseY = e.clientY - rect.top;

    setDragState({
      isDragging: true,
      tableId,
      startPos: { x: mouseX, y: mouseY },
      offset: {
        x: mouseX - table.x,
        y: mouseY - table.y,
      },
    });

    selectTable(tableId);
  }, [tables, selectTable]);

  const handleMouseMove = useCallback((e: React.MouseEvent) => {
    if (!dragState.isDragging || !dragState.tableId) return;

    const rect = canvasRef.current?.getBoundingClientRect();
    if (!rect) return;

    const mouseX = e.clientX - rect.left;
    const mouseY = e.clientY - rect.top;

    const newX = Math.max(0, Math.min(
      canvasSize.width - (tables.find(t => t.id === dragState.tableId)?.w || 0),
      mouseX - dragState.offset.x
    ));
    const newY = Math.max(0, Math.min(
      canvasSize.height - (tables.find(t => t.id === dragState.tableId)?.h || 0),
      mouseY - dragState.offset.y
    ));

    moveTable(dragState.tableId, newX, newY);
  }, [dragState, moveTable, tables, canvasSize]);

  const handleMouseUp = useCallback(() => {
    setDragState({
      isDragging: false,
      tableId: null,
      startPos: { x: 0, y: 0 },
      offset: { x: 0, y: 0 },
    });
  }, []);

  // Handle canvas click (deselect table)
  const handleCanvasClick = useCallback((e: React.MouseEvent) => {
    if (e.target === e.currentTarget) {
      selectTable(null);
    }
  }, [selectTable]);

  return (
    <div className="relative bg-gray-50 border-2 border-dashed border-gray-200 rounded-lg overflow-hidden">
      <div
        ref={canvasRef}
        className="relative cursor-crosshair"
        style={{
          width: canvasSize.width,
          height: canvasSize.height,
          backgroundImage: snapToGrid 
            ? `radial-gradient(circle, #cbd5e1 1px, transparent 1px)`
            : 'none',
          backgroundSize: snapToGrid ? `${gridSize}px ${gridSize}px` : 'auto',
        }}
        onClick={handleCanvasClick}
        onMouseMove={handleMouseMove}
        onMouseUp={handleMouseUp}
        onMouseLeave={handleMouseUp}
      >
        {/* Grid Layer */}
        <GridLayer />

        {/* Zone indicators */}
        {zones
          .filter(zone => zone.visible)
          .map(zone => {
            const tablesInZone = tables.filter(t => t.zoneId === zone.id);
            if (tablesInZone.length === 0) return null;

            // Calculate zone bounds
            const minX = Math.min(...tablesInZone.map(t => t.x));
            const minY = Math.min(...tablesInZone.map(t => t.y));
            const maxX = Math.max(...tablesInZone.map(t => t.x + t.w));
            const maxY = Math.max(...tablesInZone.map(t => t.y + t.h));

            return (
              <div
                key={zone.id}
                className="absolute pointer-events-none border-2 border-dashed rounded-lg"
                style={{
                  left: minX - 10,
                  top: minY - 30,
                  width: maxX - minX + 20,
                  height: maxY - minY + 40,
                  borderColor: zone.color,
                  backgroundColor: `${zone.color}10`,
                }}
              >
                <div
                  className="absolute -top-6 left-2 px-2 py-1 rounded text-xs font-semibold"
                  style={{
                    backgroundColor: zone.color,
                    color: 'white',
                  }}
                >
                  {zone.name}
                </div>
              </div>
            );
          })}

        {/* Tables */}
        {tables.map((table) => (
          <TableNode
            key={table.id}
            table={table}
            isSelected={selectedTable?.id === table.id}
            isDragging={dragState.isDragging && dragState.tableId === table.id}
            onMouseDown={(e) => handleMouseDown(e, table.id)}
            onClick={() => selectTable(table.id)}
          />
        ))}

        {/* Drag Controller */}
        <DragController />
      </div>
      
      {/* Canvas info */}
      <div className="absolute bottom-2 right-2 text-xs text-gray-500 bg-white px-2 py-1 rounded shadow">
        {tables.length} tables • {canvasSize.width}×{canvasSize.height}px
        {snapToGrid && ` • Grid: ${gridSize}px`}
      </div>
    </div>
  );
}
